<template>
  <div class="testest">
    <h1>test</h1>
    <p>測試computed範圍</p>
    <button @click.prevent="testf">TTTT</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      a: 1,
      b: 2,
    };
  },
  computed: {
    sum() {
      return this.a + this.b;
    },
  },
  methods: {
    testf() {
      console.log(this.sum);
      this.a = 3;
      console.log(this.sum);
    },
  },
};
</script>
